<template>
  <div class="keywords">
    <common-table
      ref="table"
      :headers="headers"
      :api="pageAPI"
      :params="params"
      :columns="columns"
      :deleteApi="deleteAPI"
      uploadURL="/api-customer/community/keyword/import"
      :modelUrl="modelUrl"
      :settings="['setting', 'upload']"
      @editRow="editRow"
      @add="add"
    >
      <template v-slot:table-header>
        <table-search
          v-model="params"
          resettable
          :columns="searchColumns"
          @search="search"
        ></table-search>
      </template>
    </common-table>
    <edit-dialog
      v-if="visibleDialog"
      :visibleDialog.sync="visibleDialog"
      :title="title"
      :columns="columns"
      :loading="loading"
      :form="form"
      @save="submit"
      @formUpdate="formUpdate"
    ></edit-dialog>
  </div>
</template>

<script>
import CommonTable from "@/components/commonTable/table";
import EditDialog from "@/components/commonTable/editDialog";
import TableSearch from "@/components/commonTable/tableSearch.vue";

import { pageAPI, deleteAPI, addAPI, updateAPI } from "@/api/keywords/index";
import { queryCaseDimensionsCascadeAPI } from "@/api/case";

export default {
  name: "case",
  components: {
    CommonTable,
    EditDialog,
    TableSearch
  },
  data() {
    return {
      pageAPI,
      deleteAPI,
      visibleDialog: false,
      loading: false,
      type: "add",
      title: "新增关键词",
      case: [],
      modelUrl: "",
      params: {},
      dimensionTree: [],
      commonOptions: {
        caseDimensions: [],
        caseReasons: [],
        subCaseReasons: []
      },
      searchOptions: {
        caseDimensions: [],
        caseReasons: [],
        subCaseReasons: []
      },
      headers: [
        { prop: "index", label: "序号" },
        { prop: "caseDimensionName", label: "案件维度" },
        { prop: "caseReasonName", label: "一级案由" },
        { prop: "subCaseReasonName", label: "二级案由" },
        { prop: "keyword", label: "关键词" },
        { prop: "updateTime", label: "修改时间", type: "date" },
        { prop: "userName", label: "添加人" },
        { prop: "action", label: "操作", width: 100, fixed: "right" }
      ],
      form: {},
      formData: {}
    };
  },
  computed: {
    searchColumns() {
      return [
        {
          prop: "caseDimensionId",
          label: "案件维度",
          type: "select",
          options: this.commonOptions.caseDimensions
        },
        {
          prop: "caseReasonId",
          label: "一级案由",
          type: "select",
          options: this.searchOptions.caseReasons
        },
        {
          prop: "subCaseReasonId",
          label: "二级案由",
          type: "select",
          options: this.searchOptions.subCaseReasons
        },
        {
          prop: "keyword",
          label: "关键词",
          type: "input"
        }
      ];
    },
    columns() {
      return [
        {
          prop: "caseDimensionId",
          label: "案件维度",
          required: true,
          type: "select",
          options: this.commonOptions.caseDimensions
        },
        {
          prop: "caseReasonId",
          label: "一级案由",
          type: "select",
          required: true,
          options: this.commonOptions.caseReasons
        },
        {
          prop: "subCaseReasonId",
          label: "二级案由",
          type: "select",
          required: true,
          options: this.commonOptions.subCaseReasons
        },
        {
          prop: "keyword",
          label: "关键词",
          type: "text",
          required: true
        }
      ];
    }
  },
  watch: {
    "params.caseDimensionId"(v) {
      this.dimensionChange(v, "search");
    },
    "params.caseReasonId"(v) {
      this.caseReasonChange(v, "search");
    },
    "formData.caseDimensionId"(v) {
      this.dimensionChange(v);
    },
    "formData.caseReasonId"(v) {
      this.caseReasonChange(v);
    }
  },
  created() {
    this.getOptions();
    let token = this.$store.state.auth.token;
    this.modelUrl = `/api-customer/community/getFile?token=${token}&type=1`;
  },
  methods: {
    getOptions() {
      this.getCaseDimensions();
    },
    async getCaseDimensions() {
      this.dimensionTree = await queryCaseDimensionsCascadeAPI();
      this.commonOptions.caseDimensions = (this.dimensionTree || []).map(
        item => {
          return { id: item.code, name: item.name };
        }
      );
    },
    search() {
      this.$refs.table.onQuery();
    },
    editRow(row) {
      this.type = "edit";
      this.title = "编辑关键词";
      this.form = row;
      this.commonOptions.caseReasons = this.getTreeChildByCode(
        this.form.caseDimensionId,
        1
      );
      this.commonOptions.subCaseReasons = this.getTreeChildByCode(
        this.form.caseReasonId,
        2
      );
      this.visibleDialog = true;
    },
    add() {
      this.type = "add";
      this.title = "新增关键词";
      this.form = {};
      this.visibleDialog = true;
    },
    submit(form) {
      let saveForm = Object.assign({}, form);
      if (this.checkForm(saveForm)) {
        let api = this.type === "add" ? addAPI : updateAPI;
        this.loading = true;
        api(saveForm)
          .then(() => {
            this.$message.success(`${this.title}成功！`);
            this.visibleDialog = false;
            this.$refs.table.refresh();
          })
          .catch(err => {
            this.$message.error(`${this.title}失败：${err}`);
          })
          .finally(() => {
            this.loading = false;
          });
      }
    },
    checkForm(form) {
      if (form) {
        return true;
      }
    },
    formUpdate(form) {
      this.formData = form;
    },
    dimensionChange(id, type) {
      if (type === "search") {
        this.params.caseReasonId = "";
        this.params.caseSubReasonId = "";
        this.searchOptions.caseReasons = this.getTreeChildByCode(id, 1);
      } else {
        this.formData.caseReasonId = "";
        this.formData.caseSubReasonId = "";
        this.formData.caseKeyword = [];
        this.commonOptions.caseReasons = this.getTreeChildByCode(id, 1);
      }
    },
    caseReasonChange(id, type) {
      if (type === "search") {
        this.params.caseSubReasonId = "";
        this.searchOptions.subCaseReasons = this.getTreeChildByCode(id, 2);
      } else {
        this.formData.caseSubReasonId = "";
        this.formData.caseKeyword = [];
        this.commonOptions.subCaseReasons = this.getTreeChildByCode(id, 2);
      }
    },
    getTreeChildByCode(code, level) {
      if (!code && code !== 0) {
        return [];
      }
      let list = [];
      switch (level) {
        case 1:
          list = this.dimensionTree;
          break;
        case 2:
          (this.dimensionTree || []).forEach(item => {
            list.push(...(item.child || []));
          });
          break;
      }
      let result = [];
      list.forEach(item => {
        if (code === item.code) {
          result = (item.child || []).map(v => {
            return { id: v.code, name: v.name };
          });
        }
      });
      return result;
    }
  }
};
</script>
