<template>
  <div class="tabs-box">
    <el-tabs v-model="activeName">
      <el-tab-pane name="strategy" @click.native="activeName = 'strategy'">
        <span slot="label" style="padding: 0 6px">策略维护</span>
        <common-table
          ref="table"
          :headers="headers"
          :api="pageAPI"
          :params="params"
          :columns="columns"
          :canDelete="false"
          :settings="['setting']"
          @editRow="editRow"
        >
          <template v-slot:header-left>
            <el-button type="primary" @click="add">
              <i class="el-icon-plus"></i>新增策略
            </el-button>
            <el-button @click="setEva">
              评价设置
            </el-button>
          </template>
          <template v-slot:table-header>
            <table-search
              v-model="params"
              resettable
              :columns="searchColumns"
              @search="search"
            ></table-search>
          </template>
          <template v-slot:action-prepend="{ row }">
            <el-button @click="concatRow(row)" type="text">关联</el-button>
          </template>
        </common-table>
        <edit-dialog
          v-if="visibleDialog"
          :visibleDialog.sync="visibleDialog"
          :title="title"
          :columns="columns"
          :loading="loading"
          :form="form"
          @save="submit"
        >
          <template v-slot:assessmentPolicyDetail>
            <form-table
              v-model="assessmentPolicyDetail"
              :columns="formColumns"
              no-add
              pagination
              :required="[
                'caseDimensionId',
                'assessmentType',
                'singleThreshold'
              ]"
            >
              <template v-slot:singleScore="{ data }">
                <span>{{
                  (data.scope.row.percent / 100) *
                    data.scope.row.singleThreshold || 0
                }}</span>
              </template>
            </form-table>
            <div style="position: absolute;top: 58px;right:0">
              <el-button size="small" type="primary" @click="saveDetailList">
                保存所有策略明细
              </el-button>
            </div>
          </template>
        </edit-dialog>
        <eva-setting :visibleDialog.sync="showEva"></eva-setting>
        <concat-setting
          :visibleDialog.sync="showConcat"
          :data="concatData"
        ></concat-setting>
      </el-tab-pane>
      <el-tab-pane name="keywords" @click.native="activeName = 'keywords'">
        <span slot="label" style="padding: 0 6px">关键词维护</span>
        <keywords></keywords>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import CommonTable from "@/components/commonTable/table";
import EditDialog from "@/components/commonTable/editDialog";
import FormTable from "@/components/form/formTable";
import TableSearch from "@/components/commonTable/tableSearch.vue";
import Keywords from "./components/keywords";
import EvaSetting from "./components/evaSetting";
import ConcatSetting from "./components/concatSetting";
import { getAssessmentType } from "@/config/dictionary";

import {
  pageAPI,
  addAPI,
  updateAPI,
  enablePolicyAPI
} from "@/api/strategy/index";
import { queryCaseDimensionsCascadeAPI } from "@/api/case/index";
import { listAPI as evaluateListAPI } from "@/api/strategy/evaluate";

export default {
  name: "Strategy",
  components: {
    CommonTable,
    EditDialog,
    FormTable,
    TableSearch,
    Keywords,
    EvaSetting,
    ConcatSetting
  },
  data() {
    return {
      pageAPI,
      activeName: "strategy",
      visibleDialog: false,
      loading: false,
      showEva: false,
      showConcat: false,
      concatData: {},
      type: "add",
      title: "新增策略",
      strategy: [],
      params: {},
      scoreLevels: [],
      caseDimensiontree: [],
      dimensionList: [],
      headers: [
        { prop: "index", label: "序号" },
        { prop: "policyName", label: "策略名称" },
        { prop: "relationCommunityNumber", label: "关联社区" },
        { prop: "userName", label: "创建人" },
        { prop: "updateTime", label: "更新时间", type: "date" },
        { prop: "action", label: "操作", width: 100, fixed: "right" }
      ],
      searchColumns: [
        {
          prop: "policyName",
          label: "策略名称",
          type: "input"
        }
      ],
      columns: [
        {
          prop: "policyName",
          label: "策略名称",
          type: "text",
          required: true,
          cols: 4
        },
        {
          prop: "assessmentInfo",
          label: "策略设置",
          type: "title",
          cols: 4
        },
        {
          prop: "assessmentPolicyDetail",
          type: "custom",
          cols: 4
        }
      ],
      form: { assessmentPolicyDetail: [] },
      assessmentPolicyDetail: [],
      formColumns: [
        {
          prop: "caseDimensionName",
          label: "案件维度",
          type: "readOnly"
        },
        {
          prop: "caseReasonName",
          label: "一级案由",
          type: "readOnly"
        },
        {
          prop: "subCaseReasonName",
          label: "二级案由",
          type: "readOnly"
        },
        {
          prop: "assessmentType",
          label: "考核方式",
          type: "select",
          options: getAssessmentType()
        },
        {
          prop: "singleThreshold",
          label: "案由基础分",
          type: "text",
          append: "分"
        },
        { prop: "percent", label: "引导系数", type: "text", append: "%" },
        { prop: "singleScore", label: "单次扣分", type: "custom" }
      ]
    };
  },
  created() {
    this.getOptions();
  },
  methods: {
    getOptions() {
      this.getCaseDimensions();
    },
    async getCaseDimensions() {
      let res = await evaluateListAPI();
      this.scoreLevels = res.levelScore || [];
      this.caseDimensiontree = await queryCaseDimensionsCascadeAPI();
      this.dimensionList = this.getListByTree(this.caseDimensiontree);
    },
    search() {
      this.$refs.table.onQuery();
    },
    editRow(row) {
      this.type = "edit";
      this.title = "编辑策略";
      let form = JSON.parse(JSON.stringify(row));
      if (form.assessmentPolicyDetail && form.assessmentPolicyDetail.length) {
        form.assessmentPolicyDetail.forEach(v => {
          v.percent = (v.scoresPercentage * 100).toFixed(2);
          v.isEdit = 0;
        });
      }
      this.form = Object.assign({}, form, { assessmentPolicyDetail: [] });
      this.assessmentPolicyDetail = form.assessmentPolicyDetail;
      this.visibleDialog = true;
    },
    async add() {
      this.type = "add";
      this.title = "新增策略";
      this.form = { isSpecCommunityFacilities: 0 };
      this.dimensionList = this.getListByTree(this.caseDimensiontree);
      this.assessmentPolicyDetail = this.dimensionList;
      this.visibleDialog = true;
    },
    concatRow(row) {
      this.concatData = row;
      this.showConcat = true;
    },
    saveDetailList() {
      this.assessmentPolicyDetail.forEach(item => {
        item.isEdit = false;
      });
    },
    setEva() {
      this.showEva = true;
    },
    stopRow(row) {
      enablePolicyAPI({ id: row.id })
        .then(() => {
          this.$message.success(`状态切换成功！`);
          this.$refs.table.refresh();
        })
        .catch(err => {
          this.$message.error(`${this.title}失败：${err}`);
        });
    },
    submit(form) {
      let saveForm = Object.assign({}, form);
      saveForm.assessmentPolicyDetail = this.assessmentPolicyDetail;
      if (this.checkForm(saveForm)) {
        let api = this.type === "add" ? addAPI : updateAPI;
        this.loading = true;
        api(saveForm)
          .then(() => {
            this.$message.success(`${this.title}成功！`);
            this.visibleDialog = false;
            this.$refs.table.refresh();
          })
          .catch(err => {
            this.$message.error(`${this.title}失败：${err}`);
          })
          .finally(() => {
            this.loading = false;
          });
      }
    },
    checkForm(form) {
      if (!form.assessmentPolicyDetail || !form.assessmentPolicyDetail.length) {
        this.$message.error("请先填写策略设置明细！");
        return false;
      }
      let isEdit = false;
      let sameSubCaseReasonId = false;
      let subCaseReasonIds = [];
      form.assessmentPolicyDetail.forEach(v => {
        v.scoresPercentage = Number(v.percent / 100);
        v.singleThreshold = Number(v.singleThreshold);
        if (v.isEdit) {
          isEdit = true;
        }
        if (subCaseReasonIds.indexOf(v.subCaseReasonId) > -1) {
          sameSubCaseReasonId = true;
        } else {
          subCaseReasonIds.push(v.subCaseReasonId);
        }
      });
      if (isEdit) {
        this.$message.error("请先保存所有策略设置明细！");
        return false;
      }
      if (sameSubCaseReasonId) {
        this.$message.error("相同报案缘由只能设置一次扣分策略！");
        return false;
      }
      return true;
    },
    getListByTree(tree) {
      let list = [];
      tree.forEach(dimension => {
        (dimension.child || []).forEach(caseReason => {
          (caseReason.child || []).forEach(subCaseReason => {
            list.push({
              caseDimensionId: dimension.code,
              caseDimensionName: dimension.name,
              caseReasonId: caseReason.code,
              caseReasonName: caseReason.name,
              subCaseReasonId: subCaseReason.code,
              subCaseReasonName: subCaseReason.name,
              assessmentType: 1,
              singleThreshold: this.getScoreByLevel(subCaseReason.caseLevel),
              percent: 100,
              isEdit: 1
            });
          });
        });
      });
      return list;
    },
    getScoreByLevel(level) {
      let result = 0;
      (this.scoreLevels || []).forEach(item => {
        if (level === item.caseLevel) {
          result = item.baseScore;
        }
      });
      return result;
    }
  }
};
</script>
